.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.react {
  background: aqua;
  color: black;
  font-size: 48px;
  font-weight: bold;
  padding: 16px;
}

.bgTitle {
  display: flex;
  width: 100%;
  background-color: #61dafb;
  color: #fff;
  justify-content: center;
}

.topBt {
  margin-top: 10px;
}

div {
  box-sizing: border-box;
}

.list {
  width: 100%;
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}

span {
  float: right;
  cursor: pointer;
}

ul {
  list-style: none;
  font-size: 18px;
}

li {
  cursor: pointer;
}